import React, { Component } from 'react'
import './Card.css'

class Card extends Component {
  isSelected () {
    return 'card'+ ((this.props.card.selected) ? ' selected' : '')+((this.props.card.active) ? ' active' : '')
  }
  render () {
    if(this.props.card.cost === undefined) this.props.card.cost = []
    if(this.props.card.onDiscard === undefined) this.props.card.onDiscard = []
    if(this.props.card.actionCost === undefined) this.props.card.actionCost = []
    if(this.props.card.actionResult === undefined) this.props.card.actionResult = []
    if(this.props.card.provides === undefined) this.props.card.provides = []

    const cost = this.props.card.cost.map((item,k) => {
      let pre = (typeof item === 'number')?'$':''
      return (<div className={'icon _'+item} key={this.props.card.key+'c'+k} title={pre+item}>{pre+item}</div>)
    })
    const actionCost = this.props.card.actionCost.map((item,k) => {
      let pre = (typeof item === 'number')?'$':''
      return (<div className={'icon _'+item} key={this.props.card.key+'ac'+k} title={pre+item}>{pre+item}</div>)
    })
    const actionResult = this.props.card.actionResult.map((item,k) => {
      let pre = (typeof item === 'number')?'$':''
      return (<div className={'icon _'+item} key={this.props.card.key+'ar'+k} title={pre+item}>{pre+item}</div>)
    })
    const onDiscard = this.props.card.onDiscard.map((item,k) => {
      let pre = (typeof item === 'number')?'$':''
      return (<div className={'icon _'+item} key={this.props.card.key+'p'+k} title={pre+item}>{pre+item}</div>)
    })
    const provides = this.props.card.provides.map((item,k) => {
      let pre = (typeof item === 'number')?'$':''
      return (<div className={'icon _'+item} key={this.props.card.key+'p'+k} title={pre+item}>{pre+item}</div>)
    })
    const arrow = (this.props.card.actionResult.length===0) ?'icon':'icon _arrow';


    return (
      <div className={this.isSelected()} onClick={() => this.props.onClick()}>
        <div className='name'>{this.props.card.name}</div>
        <div className='cost'>{cost}</div>
        <div className='provides'>{provides}</div>
        <div className='action'>
          <div className='actionCost'>{actionCost}</div>
          <div><div className={arrow}>arrow</div></div>
          <div className='actionResult'>{actionResult}</div>
        </div>
        <div className='art'></div>
        <div className='onDiscard'>{onDiscard}</div>
      </div>
    )
  }
}

export default Card



