import React, { Component } from 'react'
import './PlayerArea.css'
import Card from './Card'

class PlayerArea extends Component {
  render () {
    const oCards = this.props.player.cards|| []
    console.log('ocards',oCards)
    const cards = oCards.map((card) => {
      return (
        <Card key={card.key} card={card} onClick={() => this.props.cardClick(card)}/>
      )
    });
    if(this.props.player.building === undefined)
    this.props.player.building = []
    const building = this.props.player.building.map((card) => {
      return (
        <Card key={card.key} card={card} onClick={() => this.props.cardClick(card)}/>
      )
    });

    return (
      <div className='player-area'>
        <div className='status'>
          <div className='name'>{this.props.player.name}</div>
          <div className='income'>$[{this.props.player.income}]</div>
          <div className='money'>${this.props.player.money}</div>
        </div>
        <div className='cards' >
          {cards}
        </div>
        <div className='building' >
          {building}
        </div>
      </div>
    )
  }
}

export default PlayerArea



