function createCard(array, n, model){
  let name = model.name.replace(/ /g, '')
  let onDiscard = (model.onDiscard)||[]
  let actionCost = (model.actionCost)||[]
  let actionResult = (model.actionResult)||[]
  let active = (model.active)||false
  for (let i = 1; i <= n; i++) {
    let k = i
    array.push(Object.assign({
      key:name+k,
      actionCost:actionCost,
      actionResult:actionResult,
      onDiscard:onDiscard,
      active:active,
    },
      model))
  }
}

export const ALL_CARDS = createDeckCards()
