import React, { Component } from 'react'
import PlayerArea from './PlayerArea'
import Card from './Card'
import './GameArea.css'
import {ST} from './config/config'

class GameArea extends Component {
  render () {
    let constCo = this.props.game.cards
    if (constCo ===undefined) constCo = []
    const cardsOffer = constCo.map((card) => {
      return (
        <Card key={card.key} card={card} onClick={()=>this.props.cardClick(card)}/>
      )
    })
    let players = this.props.game.players
    if(players === undefined) players = []
    const playerAreas = players.map((player) => {
      return (
        <PlayerArea key={player.key} player={player} cardClick={this.props.cardClick}/>
      )
    })
    const currentPlayer = players[this.props.game.currentPlayer]||{};
    return (
      <div className='game-area'>
        <div className='status'>
          <div>Current player: {currentPlayer.name}</div>
          <button disabled={[ST.BUYING_CARDS_SEL, ST.ACTION_SELECTING_CARDS].indexOf(this.props.game.status)<0 } onClick={this.props.confirmSelectionClick}>Confirm Selection</button>
          <div>{this.props.game.status}</div>
        </div>
        <div className='card-offer'>
          <div className='loan-offer' onClick={()=>this.props.loanClick()}>Loans:  {this.props.game.loans}</div>
          <div className='discard'>{this.props.game.discard?this.props.game.discard.length:0}</div>
          <div className='deck'>{this.props.game.deck?this.props.game.deck.length:0}</div>
          {cardsOffer}
        </div>
        <div className='player-areas'>
          {playerAreas}
        </div>
      </div>
    )
  }
}

export default GameArea



